<template>
  <div class="lotto-generator">
    <!-- Generate Button -->
    <button @click="generateLottoNumbers"
            :disabled="!isInputValid || lottoRecords.length >= 100"
            class="generate-button">(AUTO-) Generate</button>
    <!-- Reset Button -->
    <button @click="resetLottoNumbers" class="reset-button">Clear</button>

    <!-- Input Section -->
    <div class="box-border">
      <div class="pattern-0">
        <div class="inputs">
          <!-- User Numbers Input -->
          <input v-for="(num, index) in userNumbers"
                 :key="index"
                 type="number"
                 v-model.number="userNumbers[index]"
                 @input="validateInput(index)"
                 :disabled="lottoRecords.length >= 100"
                 placeholder="1-33">

          <!-- Powerball Input -->
          <input type="number"
                 v-model.number="powerBallNumber"
                 @input="validatePowerBallInput"
                 :disabled="lottoRecords.length >= 100"
                 placeholder="Powerball (1-16)">
        </div>
        <p class="font-s5">Enter Your Favorite Numbers (Optional)</p>
      </div>
    </div>

    <!-- Limit Notification -->
    <p v-if="lottoRecords.length >= 100">You can enter up to 100 sets.</p>

    <!-- Lotto Records Display -->
    <div v-if="lottoRecords.length > 0" class="numbers">
      <div v-for="(record, index) in lottoRecords" :key="index" :class="['record', `pattern-${index % 5 + 1}`]">
        <div class="record-header">
          <h3>1st Prize Winners</h3>
          <p class="record-date">{{ record.date }}</p>
        </div>
        <div class="record-numbers">
          <!-- Display Numbers -->
          <span v-for="(number, numIndex) in record.numbers" :key="numIndex" class="number">{{ number }}</span>
          <!-- Display Powerball -->
          <span class="number power-ball">{{ record.powerBallNumber }}</span>
        </div>
      </div>

      <!-- Record Buttons -->
      <div class="record-buttons">
        <button @click="printLottoRecords" class="print-button">Print</button>
        <button @click="downloadLottoRecords" class="download-button">Download</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      userNumbers: Array(6).fill(null), // Adjusted for 6 numbers
      powerBallNumber: null,
      lottoRecords: []
    }
  },
  computed: {
    isInputValid () {
      const uniqueNumbers = new Set(this.userNumbers.filter(num => num !== null))
      return (
        uniqueNumbers.size === this.userNumbers.filter(num => num !== null).length &&
        this.userNumbers.every(num => num === null || (num >= 1 && num <= 33)) && // Numbers range from 1 to 33
        (this.powerBallNumber === null || (this.powerBallNumber >= 1 && this.powerBallNumber <= 16)) // Powerball range from 1 to 16
      )
    }
  },
  methods: {
    validateInput (index) {
      const num = this.userNumbers[index]
      if (num < 1 || num > 33 || this.userNumbers.filter(n => n === num).length > 1) {
        this.userNumbers[index] = null
      }
    },
    validatePowerBallInput () {
      if (this.powerBallNumber < 1 || this.powerBallNumber > 16) {
        this.powerBallNumber = null
      }
    },
    generateLottoNumbers () {
      if (this.lottoRecords.length < 100 && this.isInputValid) {
        const existingNumbers = this.userNumbers.filter(num => num !== null)
        const numbers = [...existingNumbers]
        while (numbers.length < 6) { // Generate 6 numbers for each set
          const randomNumber = Math.floor(Math.random() * 33) + 1 // Random number between 1 and 33
          if (!numbers.includes(randomNumber)) {
            numbers.push(randomNumber)
          }
        }
        const powerBallNumber = this.powerBallNumber || Math.floor(Math.random() * 16) + 1 // Random powerball number between 1 and 16
        const date = new Date().toLocaleString('en-US')
        this.lottoRecords.unshift({ date, numbers: numbers.sort((a, b) => a - b), powerBallNumber })
      }
    },
    resetLottoNumbers () {
      this.lottoRecords = []
      this.userNumbers = Array(6).fill(null) // Reset to 6 numbers
      this.powerBallNumber = null
    },
    printLottoRecords () {
      const printableContent = this.lottoRecords.map(record => `${record.date}: ${record.numbers.join(', ')} Powerball: ${record.powerBallNumber}`).join('\n')
      const printWindow = window.open('', '', 'height=400,width=600')
      printWindow.document.write('<pre>' + printableContent + '</pre>')
      printWindow.document.close()
      printWindow.print()
    },
    downloadLottoRecords () {
      const blob = new Blob(
        [this.lottoRecords.map(record => `${record.date}: ${record.numbers.join(', ')} Powerball: ${record.powerBallNumber}`).join('\n')],
        { type: 'text/plain;charset=utf-8' }
      )
      const link = document.createElement('a')
      link.href = URL.createObjectURL(blob)
      link.download = 'lotto-records.txt'
      link.click()
      URL.revokeObjectURL(link.href)
    }
  }
}
</script>
<style scoped>
 @import '../assets/css/lotto.css'
</style>
